@mixin button-reset {
  display: inline-block;
  font-weight: normal;
  margin: 0;
  line-height: normal;
  border: 0;
  box-shadow: none;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  white-space: nowrap;
  user-select: none;
  border-radius: 0;
  width: auto;
  height: auto;
  background: none;
}
