.editor-block-list__layout {
  > [data-type^='gutenbee'] {
    &:first-child {
      > .editor-block-list__block-edit {
        > [data-block] {
          > div[class*='wp-block'] {
            margin-top: 0 !important;
          }
        }
      }
    }

    &:nth-last-child(3),
    &:nth-last-child(2) {
      > .editor-block-list__block-edit {
        > [data-block] {
          > div[class*='wp-block'] {
            margin-bottom: 0 !important;
          }
        }
      }
    }
  }
}
